@import url('https://rsms.me/inter/inter.css');
html { font-family: 'Inter', sans-serif; }
@supports (font-variation-settings: normal) {
  html { font-family: 'Inter var', sans-serif; }
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

::-webkit-scrollbar{
  width: 15px;
  height: 10px;
}
::-webkit-scrollbar-track{
  background-color: #1C1C1C;
}

::-webkit-scrollbar-thumb{
  background-color: #3C3C3C;
}

@media only screen and (hover: none) and (pointer: coarse){

  ::-webkit-scrollbar{
      width: unset;
      height: unset;
  }
  ::-webkit-scrollbar-track{
      background-color: unset;
  }
  
  ::-webkit-scrollbar-thumb{
      background-color: unset;
  }
  
}


*, *::before, *::after{
  box-sizing: border-box;
}
body{
  margin: 0;
  color: white;
  font-family: 'Inter', sans-serif;
  background-color: #252526;

}
.app{
  display: flex;
}
.menu{
  background-color: #1E1E1E;
  display: flex;
  height: 100vh;
  width: 30%;
  position: fixed;
  left: 0px;
  --menu-width:(left);
  word-wrap: break-word;
  
  box-shadow: 2px 0px 6px rgba(0, 0, 0, 0.75);
  
}
.options-list{
  list-style: none;
}
.options-menu{
  padding-left: 0;
  padding-top: 20px;
  margin: 0;
  width: stretch;
  overflow: hidden;
  overflow-y: scroll;
}

.question-group{
  flex-wrap: wrap;
}
.group-name{
  font-weight: 500;
  margin-left: 25px;
  font-size: 24px;
  margin-bottom: 10px;
}
.question-selector-button{
  font-size: 16px;
  color: #858578;
  text-align: left;
  font-weight: 500;
  width: stretch;
  background-color: transparent;
  border: none;
  transition: 200ms;
  cursor: pointer;
  padding-left: 40px;
  padding-top: 6px;
  padding-bottom: 6px;
  margin-bottom: 4px;
}
.question-selector-button:hover{
  background-color: #858578;
  transform: translateX(10px);
  border-radius: 20px 0px 0px 20px;
  color: #1E1E1E;
}
.question-selector-button:active{
  box-shadow: inset 0px 0px 3px rgb(0, 0, 0);
  background-color: #787d85;



}
.content{
  position: absolute;
  top: 0;
  right: 0;
  width: 70%;  
  display: flex;
  height: fit-content;
  background-color: #252526;

}
#markdown-container{
  padding-left: 40px;
  padding-top: 40px;
  width: stretch;
  height: fit-content;
  padding-right: 40px;
  
}



.emotional-support{
  position: fixed;
  right: 10px;
  bottom: 10px;
  height: 7vw;
  
  z-index: 1;

}


